import React, {useCallback, useEffect} from 'react';
import {Formik, Form, Field, ErrorMessage, useFormik} from 'formik';
import {Button, Heading, Input, ListItem, Text, UnorderedList} from '@chakra-ui/react'
import {
    FormControl,
    FormLabel,
    FormErrorMessage,
    FormHelperText,
} from '@chakra-ui/react'
import * as yup from "yup";


export const PptIForm = () => {
    const formik = useFormik({
        initialValues: {NumOfGA: 0,},
        onSubmit: (values) => {
            window.Telegram.WebApp.sendData(JSON.stringify({
                NumOfGA: formik.values.NumOfGA,
                TypeOfReport: "PptIForm",
                theme: window.Telegram.WebApp.colorScheme,
            }));
            formik.resetForm()
        },
        validationSchema: yup.object(
            {NumOfGA: yup.number().required('  ').min(0.01, '     0')}),
    })
    const sendDataToTelegram = useCallback(() => {
        formik.handleSubmit()
    }, [formik.values])
    const mainButton = window.Telegram.WebApp.MainButton;
    mainButton.text = " ";
    mainButton.enable();
    mainButton.show();

    useEffect(() => {
        window.Telegram.WebApp.onEvent('mainButtonClicked', sendDataToTelegram)
        return () => {
            window.Telegram.WebApp.offEvent('mainButtonClicked', sendDataToTelegram)
        }
    }, [sendDataToTelegram])


    return(
        <div>
            <Heading textAlign={"center"}></Heading>
            <Text textAlign={"center"} fontSize={"xl"}>    </Text>

            <form onSubmit={(e) => {
                e.preventDefault();
                formik.handleSubmit(e);
            }}>
                <FormControl m={2} isInvalid={formik.errors.NumOfGA && formik.touched.NumOfGA}>
                    <FormLabel fontSize={"md"}> </FormLabel>
                    <Input id={"NumOfGA"}
                           name={"NumOfGA"}
                           value={formik.values.NumOfGA}
                           onChange={(e) =>{
                               formik.setFieldValue('NumOfGA', e.target.value)
                           }}
                           onBlur={(e) => {
                               formik.setTouched({...formik.touched, NumOfGA: true})
                           }}
                           placeholder=' ' />
                    <FormErrorMessage>{formik.errors.NumOfGA}</FormErrorMessage>
                </FormControl>
                {mainButton === undefined ?
                    <Button
                        colorScheme='teal'
                        type='submit'
                        width={"full"}
                    >
                        Submit
                    </Button>: <span></span>
                }
            </form>
        </div>
    )}